<template>
  <div class="notes">
    <div class="container">
      <div class="notes__top">
        <h2 class="notes__top-title">
          {{ notes.length > 0 ? $t("allNotes") : $t("noNotes") }}
        </h2>
        <button class="notes__top-btn" @click="view = !view">
          <img v-if="view" src="../assets/images/list.svg" alt="" />
          <img v-else src="../assets/images/grid.svg" alt="" />
          <span>{{ view ? $t("list") : $t("grid") }}</span>
        </button>
      </div>
      <div class="notes__list" :class="{ active: !view }">
        <NotesItem
          :view="view"
          :note="note"
          v-for="note in notes"
          :key="note.id"
          @change="$emit('change', note.id)"
          @delNote="$emit('delNote', note.id)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import NotesItem from "./NotesItem.vue";
export default {
  components: { NotesItem },
  props: {
    notes: Array,
  },
  data() {
    return {
      view: true,
    };
  },
};
</script>

<style lang="scss" scoped></style>
