<template>
  <div class="modal" @click="closeModel">
    <div class="modal__block" @click.stop="">
      <h2 class="modal__block-title">
        {{ !edit ? $t("addNote") : $t("changeNote") }}
      </h2>
      <div class="modal__block-inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <input type="text" placeholder="content" v-model="content" />
        </label>
      </div>
      <div class="modal__block-btns">
        <button class="modal__block-btns-btn del" @click="closeModel">
          {{ $t("cansel") }}
        </button>
        <button
          v-if="!edit"
          class="modal__block-btns-btn edit"
          @click="addNote"
        >
          {{ $t("add") }}
        </button>
        <button v-else class="modal__block-btns-btn edit" @click="changeNote">
          {{ $t("change") }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { v4 as uuidv4 } from "uuid";
export default {
  props: {
    editedNote: Object,
    edit: Boolean,
  },
  data() {
    return {
      title: "",
      content: "",
    };
  },
  methods: {
    closeModel() {
      this.$emit("close", false);
      this.title = this.content = "";
    },
    addNote() {
      if (this.title != "" && this.content != "") {
        const note = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          text: this.content,
          id: uuidv4(),
        };
        this.$emit("addNote", note);
        this.closeModel();
      }
    },
    changeNote() {
      if (this.title != "" && this.content != "") {
        const newEditedNote = {
          title: this.title,
          date: new Date().toLocaleDateString(),
          text: this.content,
          id: this.editedNote.id,
        };
        this.$emit("changeNote", newEditedNote);
        this.closeModel();
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
