<template>
  <header class="header">
    <transition name="header__notes" mode="out-in">
      <nav class="header__nav" v-if="header">
        <button class="header__nav-lang" @click="switchLang">
          {{ $i18n.locale }}
        </button>
        <h1 class="header__nav-title">{{ $t("notes") }}</h1>
        <button class="header__nav-search" @click="header = !header">
          <img src="@/assets/images/search.svg" alt="" />
        </button>
      </nav>
      <nav class="header__search" v-else>
        <button
          class="header__search-back"
          @click="(header = !header), (search = '')"
        >
          <img src="@/assets/images/back.svg" alt="" />
        </button>
        <input
          class="header__search-input"
          autofocus
          v-model="search"
          type="text"
          :placeholder="$t('searchInput')"
        />
        <button class="header__search-clear">
          <img src="@/assets/images/clear.svg" alt="" @click="search = ''" />
        </button>
      </nav>
    </transition>
  </header>
</template>

<script>
export default {
  name: "Navbar",
  data() {
    return {
      header: true,
      search: "",
    };
  },
  watch: {
    search(newVall) {
      this.$emit("setSerch", newVall);
    },
  },
  methods: {
    switchLang() {
      this.$i18n.locale == "ru"
        ? (this.$i18n.locale = "uz")
        : (this.$i18n.locale = "ru");
      localStorage.lang = this.$i18n.locale;
    },
  },
};
</script>
