<template>
  <div class="wrapper">
    <Navbar @setSerch="searchValue = $event" />
    <NotesList :notes="filterNote" @change="change" @delNote="delNote" />
    <AddBtn @click="(isModalOpen = true), (edit = false)" />
    <Model
      :editedNote="editedNote"
      :edit="edit"
      @addNote="addNote"
      @close="isModalOpen = $event"
      @changeNote="changeNote"
      v-show="isModalOpen"
    />
  </div>
</template>

<script>
import AddBtn from "@/components/AddBtn.vue";
import Navbar from "@/components/Navbar.vue";
import NotesList from "@/components/NotesList.vue";
import Model from "./components/Model.vue";
export default {
  name: "App",
  components: { Navbar, NotesList, AddBtn, Model },
  data() {
    return {
      isModalOpen: false,
      notes: [],
      editedNote: null,
      edit: false,
      searchValue: "",
    };
  },
  methods: {
    addNote(note) {
      /* this.notes.push(note); */
      this.notes = [...this.notes, note];
    },
    change(id) {
      this.isModalOpen = this.edit = true;
      let currentNote = this.notes.find((note) => note.id == id);
      this.editedNote = currentNote;
    },
    changeNote(newNote) {
      this.notes.forEach((note) => {
        if (note.id == newNote.id) {
          note.title = newNote.title;
          note.text = newNote.text;
          note.date = newNote.date;
        }
      });
    },
    delNote(id) {
      this.notes = this.notes.filter((note) => note.id != id);
    },
    getNotes() {
      let localNotes = localStorage.notes;
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    },
    /* getNotes() {
      let localNotes = localStorage.getItem("notes");
      if (localNotes) {
        this.notes = JSON.parse(localNotes);
      }
    }, */
  },
  mounted() {
    this.getNotes();
  },
  computed: {
    filterNote() {
      return this.searchValue
        ? this.notes.filter((note) => {
            return note.title
              .toLowerCase()
              .includes(this.searchValue.toLowerCase());
          })
        : this.notes;
    },
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true,
    },
    /* notes: {
      handler() {
        localStorage.setItem("notes", JSON.stringify(this.notes));
      },
      deep: true,
    }, */
  },
};
</script>
